import React, { useEffect } from 'react';
import { withCookies } from 'react-cookie';
import { useDispatch } from 'react-redux';
import { postLogout, postAuth } from '../../Redux/actions';
import { useHistory } from 'react-router-dom';

const MainPage = ({ cookies }) => {
  const dispatch = useDispatch();
  const history = useHistory();

  console.log(cookies.cookies.access);

  useEffect(() => {
    if (!cookies.cookies.access) {
      history.push('/');
    }
  }, [cookies.cookies.access]);

  const logOut = () => {
    dispatch(postAuth(null, null));
    dispatch(postLogout(cookies.cookies.access));
    cookies.remove('access', { path: '/', domain: 'fire567.github.io' });
    console.log(cookies.cookies.access);
    location.reload();
  };

  return <div onClick={() => logOut()}>Выход</div>;
};

export default withCookies(MainPage);
